@import "../../commonstyles/variables.scss";

.disclaimer {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  h2 {
    margin-bottom: 20px;
    text-align: center;
  }
  .content {
    position: relative;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   .desc {
     width: 70%;
     max-width: 700px;
     background-color: white;
     padding: 20px 20px 0 20px;
     max-height: 80%;
     overflow-y: auto;
     .action{
       margin-top: 20px;
       justify-content: flex-end;
       position: sticky;
       bottom: -1px;
       padding: 20px 0;
       background-color: white;
       button:not(:last-child) {
         margin-right: 16px;
       }
     }
   }
  }
}
